export default {
    name: 'Impressum',

    data() {
        return {
            impressum: []
        }
    },
    async fetch() {
        const { json: data } = await this.$kirby.find({
            "query": "page('impressum')",
            "select": {
                "title": true,
                "impressumtitle": true,
                "impressumtext": true,
                "contacttitle": true,
                "contacttext": true,
            }
        }, this.$nuxt.context.app.i18n.locale)
        this.impressum = data
    }
}
